import * as yup from "yup";

const schema = yup.object({
  username: yup
    .string()
    .min(2, "     4 ")
    .max(100, "     100 ")
    .required(' ""   '),
  email: yup
    .string()
    .email("  email-")
    .required(' "email"   '),
  password1: yup
    .string()
    .min(8, "     8 ")
    .required(' ""   '),
  password2: yup
    .string()
    .min(8, "     8 ")
    .oneOf([yup.ref("password1")], "  ")
    .required(' " "   '),
  keyword: yup
    .string()
    .min(4, "      4 ")
    .required(' " "   '),
});

export default schema;
